interface IModalConfirmData {
  title: string,
  btnApply: string,
}

export type ModalConfirmActionType = 'update_account' | 'delete_user' | 'delete_account' | 'delete_post' | 'delete_comment';

export const ModalConfirmData: { [action: string]: IModalConfirmData } = {
  delete_user: {
    title: ' ?',
    btnApply: ', ',
  },
  delete_account: {
    title: ' ?',
    btnApply: ', ',
  },
  delete_post: {
    title: ' ?',
    btnApply: ', ',
  },
  delete_comment: {
    title: ' ?',
    btnApply: ', ',
  },
  update_account: {
    title: ' ?',
    btnApply: ', ',
  },
};

export const isModalDelete = (action: ModalConfirmActionType) => {
  return action.includes('delete');
};